/*Basicos*/
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

/*Fuente del texto con su tamano e interlineado*/
body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8; }

/*Tamano y color de los parrafos*/
p {
  color: #000000;
  font-size: 2rem; }

/*Basicos de un contenedor*/
.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

/*Quitamos subrayado de los links*/
a {
  text-decoration: none; }

/*Basicos de una imagen*/
img,
picture {
  width: 100%;
  display: block; }

/*Fuente, basicos de encabezados*/
h1, h2, h3, h4 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center; }

/*Tamanos de los encabezados*/
h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

/* Configuracion inicial de una seccion*/
.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem; }

/* Centrar el contenido */
.contenido-centrado {
  max-width: 800px; }

/*Eliminar las margenes de cualquier elemento */
.no-margin {
  margin: 0; }

/* Alinear a la derecha */
.alinear-derecha {
  display: flex;
  justify-content: flex-end; }

/* Alinear a la izquierda */
.alinear-izquierda {
  display: flex;
  justify-content: flex-start; }

/* Deshabilitar cambios en el puntero del mouse */
.deshabilitar {
  pointer-events: none; }

/* Colocar el texto en negrilla*/
.bold {
  font-weight: 700; }

/* Colocar el elemento al msimo tamano de la pantalla*/
.w-100 {
  width: 100%;
  background-color: #5c0808; }

/*Boton amarillo en linea*/
.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none; }
  .boton-amarillo:hover {
    color: #000000;
    background-color: #db8409;
    cursor: pointer; }

/*Boton amarillo en bloque*/
.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
  margin-top: 1rem;
  margin-bottom: 0; }
  .boton-amarillo-block:hover {
    color: #000000;
    background-color: #db8409;
    cursor: pointer; }

/*Boton verde en linea*/
.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none; }
  .boton-verde:hover {
    color: #000000;
    background-color: #6eac00;
    cursor: pointer; }

/*Boton rojo en bloque*/
.boton-rojo-block {
  background-color: #5c0808;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
  margin-top: 1rem;
  margin-bottom: 0; }
  .boton-rojo-block:hover {
    color: #000000;
    background-color: #570808;
    cursor: pointer; }

.dark-mode {
  background-color: #1a1a1a; }
  .dark-mode p, .dark-mode a {
    color: #FFFFFF; }
  .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
    color: #e1e1e1; }
  .dark-mode .anuncio {
    background-color: #333333;
    border: 1px solid #333333; }
    .dark-mode .anuncio .precio {
      color: #71B100; }
  .dark-mode .iconos-caracteristicas img {
    filter: invert(100%); }
  .dark-mode .formulario label {
    color: #FFFFFF; }
  .dark-mode .formulario legend {
    color: #e1e1e1; }
  .dark-mode .formulario p {
    color: #e1e1e1; }
  .dark-mode .formulario input:not([type="submit"]), .dark-mode .formulario textarea, .dark-mode .formulario select {
