/* Internas */
.alerta {
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: bold;
  margin: .5rem 0; }
  .alerta.error {
    background-color: #5c0808;
    padding: .5rem; }
  .alerta.exito {
    background-color: #71B100;
    padding: .5rem; }

table.propiedades, table.vendedores {
